<template>
  <div class="home" id="home">
    <NavBar :showNav="showNav" @scrollTo="scrollTo"></NavBar>
    <section class="home-container">
      <div class="home-bg">
        <div class="avatar"></div>
        <div class="welcome">
          <p class="welcome-text-green">welcome</p>
          <p class="welcome-text-white">to</p>
          <p class="welcome-text-white">my world</p>
        </div>
        <div class="started">
          <a  @click="scrollTo('about')" class=" btn btn-default">GET STARTED</a>
        </div>
      </div>
    </section>
    <section style="display: flex;justify-content: center">
      <div style="height: 150px; margin-top: -40px;float:left;width:30.333%"></div>
      <div style="height: 150px; margin-top: -40px;float:left;width:30.333%"></div>
      <div style="height: 150px; margin-top: -40px;float:left;width:30.333%"></div>
    </section>
    <AboutMe :showAboutAnimate="showAboutAnimate"></AboutMe>
    <Skills :showSkillsAnimate="showSkillsAnimate"></Skills>
    <Blog :showBlogAnimate="showBlogAnimate"></Blog>
    <MyWorks :showWorksAnimate="showWorksAnimate"></MyWorks>
  </div>
</template>

<script>
import NavBar from "@/components/navBar/NavBar.vue";
import AboutMe from "@/components/aboutMe/AboutMe.vue";
import Skills from "@/components/skills/Skills.vue";
import MyWorks from "@/components/myWorks/myWorks.vue";
import Blog from "@/components/blog/Blog.vue";


export default {
  name: 'Home',
  mounted() {
    window.addEventListener('scroll',this.handelScroll)
  },
  destroyed() {
    window.removeEventListener('scroll',this.handelScroll)
  },
  data(){
    return {
      showNav: false,
      scrollTop: 0,
      targetOffsetTop: 0,
      STEP: 30,
      showAboutAnimate: false,
      showSkillsAnimate: false,
      showNavCollapseProp: false,
      showWorksAnimate: false,
      showBlogAnimate: false
    }
  },
  components: {
    NavBar,
    AboutMe,
    Skills,
    MyWorks,
    Blog
  },
  methods: {
    handelScroll: function () {
      this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      this.showNav = this.scrollTop > 0;
      let aboutOffsetTop = document.querySelector(`#about`).offsetTop;
      let skillsOffsetTop = document.querySelector(`#skills`).offsetTop;
      let worksOffsetTop = document.querySelector(`#works`).offsetTop;
      let blogOffsetTop = document.querySelector(`#blog`).offsetTop;
      if (this.scrollTop >= (aboutOffsetTop - 550)) this.showAboutAnimate = true;
      if (this.scrollTop >= (skillsOffsetTop - 550)) this.showSkillsAnimate = true;
      if (this.scrollTop >= (worksOffsetTop - 550)) this.showWorksAnimate = true;
      if (this.scrollTop >= (blogOffsetTop - 550)) this.showBlogAnimate = true;
    },
    scrollTo(el){
      this.targetOffsetTop = document.querySelector(`#${el}`).offsetTop;
      if( this.scrollTop > this.targetOffsetTop ){
        this.smoothUp();
      }else {
        this.smoothDown();
      }
    },
    smoothDown() {
      if (this.scrollTop < this.targetOffsetTop) {
        if (this.targetOffsetTop - this.scrollTop < this.STEP){
          this.scrollTop = this.targetOffsetTop;
        } else {
          this.scrollTop += this.STEP;
        }
        document.documentElement.scrollTop = this.scrollTop;
        document.body.scrollTop = this.scrollTop;
        requestAnimationFrame(this.smoothDown);
      }
    },
    smoothUp(){
      if (this.scrollTop > this.targetOffsetTop) {
        if (this.scrollTop - this.targetOffsetTop < this.STEP){
          this.scrollTop = this.targetOffsetTop;
        } else {
          this.scrollTop -= this.STEP;
        }
        document.documentElement.scrollTop = this.scrollTop;
        document.body.scrollTop = this.scrollTop;
        requestAnimationFrame(this.smoothUp);
      }
    }
  }
}
</script>
